.pull-right {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
}

.navBar{
    width: 100%;
}
.h1 {
    margin-top: 10px !important;
    margin-bottom: 20px !important;
}

.App-css {
    text-align: center;
    margin: 0;
    width: 100%;
}

.AllPanels {
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
}

.panelsLeft {
    width: 40%;
    margin-left: 2%;
}

.panelsNoticias {
    width: 60%;
    margin-right: 2%;
}

.cardNoticia {
    padding: 0 0 0 0;
    margin: 0 0 3% 0;
    width: 100%;
    border: black 2px solid;
    border-radius: 10px;
    box-sizing: content-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    cursor: pointer;
}
.cardImg {
    width: 100%;
}

.panelNotice {
    width: 30%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100px;
    margin-left: 3%;
    margin-top: 3%;
}

.textoNoticia {
    color: black;
    text-align: justify;
    margin-left: 3%;
    margin-right: 3%;
}

.acessoRapido {
    width: 45%;
    height: 200px;
    margin: 0 5px 10px;
    cursor: pointer;
}

.canvasDiasAcidentes {
    position: absolute;
}

.corpo h1{
    text-align: center;
}

.content{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.indice{
    position: fixed;
    width: 20%;
    height: 300px;
    margin-left: 5%;
    border: 2px solid black;
    border-radius: 5px;
}

.indice li{
    list-style-type: none;
    color: darkgray;
    transition: color 2s;
}

.indice li:hover{
    color: black;
}

.indice ul{
    padding: 5%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: start;
        align-items: flex-start;
}

.corpo li{
    color: darkgray;
    transition: color 2s;
}

.corpo li:hover{
    color: black;
}

.textoSobre{
    margin-left: 5%;
    margin-right: 5%;
    text-align: justify;
    color: darkgray;
    transition: color 2s;
    cursor: default;
}

.textoSobre:hover{
    color: black;
}

.corpo{
    width: 70%;
    height: auto;
    margin-right: 5%;
    border: 2px solid black;
    border-radius: 5px;
}

.fantasma{
    width: 30%;
    height: 100px;
}

.partCorpo p{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

@media only screen and (max-width: 600px) {

    .AllPanels {
        width: 100%;
        height: 100%;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
    }

    .panelsLeft {
        width: 100%;
        margin-left: 2%;
    }

    .panelsNoticias {
        width: 100%;
        margin-right: 2%;
    }

    .panelNotice {
        width: 70%;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: center;
            justify-content: center;
        height: 100px;
        margin-left: 3%;
        margin-top: 3%;
    }

    .tableReact {
        width: 100%;
    }

    .navBar {
        text-align: center;
    }
}